import React from 'react';

const Tablehead = () => {
    return (
        <thead>
            <tr>
                <th>Name</th>
                <th>Job</th>
                <th>Remove</th>
            </tr>
        </thead>
    );
};

function Tablebody() {
    return(
            <tbody>
                <tr>
                    <td>Luis</td>
                    <td>Programador</td>
                    {/* <td><Deletebutton/></td>      */}
                </tr>
                <tr>
                    <td>Noelia</td>
                    <td>Profesora</td>
                    {/* <td><Deletebutton/></td>     */}
                </tr>
                <tr>
                    <td>Ada</td> 
                    <td>Cientifica</td>
                    {/* <td><Deletebutton/></td>     */}
                </tr>
            </tbody>
        //una pregunta: se puede ponere en div tabla, deb esta forma me trae conflicto?
    );
}

const Table = () => {
    return (
        <table>
            <Tablehead/>
            <Tablebody/>
        </table>
    );
};

export default Table;